body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
}

.app {
  background-color: #fafafa;
}

.app__headerImage {
  height: 3em;
  width: 8em;

}

.app__header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
  padding: 20px;
  background: white;
  object-fit: contain;
  border-bottom: 1px solid lightgrey;
  display: flex;
  justify-content: space-between;
  object-fit: contain;
}

.app__signup{
  display: flex;
  flex-direction: column;
}
.app__posts {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  
}

.post {
    background-color: white;
    max-width: 500px;
    border: 1px solid lightgrey;
    margin-bottom: 45px;
}

.post__image {
    width: 100%;
    object-fit: contain;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgrey;
}

.post__text {
    font-weight: normal;
    padding: 20px;
}

.post__header {
    padding: 20px;
    display: flex;
    align-items: center;
}

.post__avatar {
    margin-right: 10px;
}
.post__commentBox{
    display: flex;
    margin-top: 10px;
  }
.post__comments{
    padding: 10px;
  }
.post__input{
  flex: 1 1;
  border: none;
  padding: 10px;
  border-top: 1px solid lightgray;
  }
.post__button{
    flex: 0 1;
    border: none;
    color:#6082a3;
    background-color: transparent;
    border-top: 1px solid lightgray;
  
  }
  @media screen and (min-width: 550px) {
    .post__image {
      width: 100%;
      min-width: 500px;
      max-width: 500px;
      object-fit: contain;
      border-top: 1px solid lightgray;
      border-bottom: 1px solid lightgrey;
  }
}
.imageupload{
    display: flex;
    flex-direction: column;
    width: 70%;
    min-width: 500px;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    margin-top:  10px;
    margin-bottom: 10px;
    background-color: white;
    border-top: 1px solid lightgray;
  
  }
  .imageupload__progress{
    width:100%;
  }
